___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}

:root {
  --primary-color: #fff;
  --dropdown-hover: #f5ebe0;
  --text-color-1: #20201d;
  --text-color-2: #fff;
  --text-color-1-hover: #b7965c;
  --border-input: #20201d;
  --border-input-focus: #b7965c;
  --button-color: #b7965c;
  --button-color-hover: #af7d27;
  --form-control-border: #cccc;
  --scrollbar-color: #424168;
  --panel-color: #eaeaea;
}

body {
  background-color: var(--primary-color);
}

/* ============= TOATS ============= */

.toast-success {
  background-color: #5a58a4 !important;
}

.toast-warning {
  background-color: #f0cd81 !important;
}

.toast-error {
  background-color: #ff537e !important;
}

/* ============= BUTTON =========== */
.button {
  padding: 0.8rem 1rem;
  font-size: 0.9rem;
  color: var(--text-color-2);
  background-color: var(--button-color);
  border: none;
  outline: none;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: all 0.2s;
}

.button:hover {
  background-color: var(--button-color-hover);
}

.button-icon {
  background-color: var(--button-color);
  border-radius: 0.2rem;
  border: none;
  outline: none;
  padding: 0.6rem 0.7rem;
  cursor: pointer;
  transition: all 0.2s;
}

.button-icon:hover {
  background-color: var(--button-color-hover);
}

.button-icon i {
  font-size: 1.5rem;
  color: var(--text-color-2);
}

.button-close {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 0 0.5rem 0 0.5rem;
  padding: 0.6rem 0.85rem;
}

/* ============= SEARCH =========== */

.search-title {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.search-title h2.title {
  font-size: 2rem;
  color: var(--button-color);
  margin-bottom: 0;
}

.search-title .search-button-back {
  background: transparent;
  border: none;
  outline: none;
  padding: 0.5rem 0.6rem;
  cursor: pointer;
}

.search-title .search-button-back i {
  font-size: 1.8rem;
  color: var(--button-color);
}

/* =========== LOADER ===========  */

.loader-box {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.loader {
  position: absolute;
  left: 135px;
  display: none;
  align-items: center;
  transition: opacity 0.75s, visibility 0.75s;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader::after {
  content: '';
  width: 16px;
  height: 16px;
  border: 6px solid #dddddd;
  border-top-color: var(--scrollbar-color);
  border-radius: 50%;
  animation: loading 0.75s ease infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}

/* ============= NAVBAR =========== */
header {
  position: sticky;
  top: 0;
  z-index: 10;
  padding: 1rem 1rem;
  background-color: var(--primary-color);
}

nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo,
.nav-menu a,
.dropdown-menu a {
  text-decoration: none;
}

.nav-menu a,
.dropdown-menu a {
  text-decoration: none;
  padding: 0.7rem 0;
}

.logo {
  width: 8rem;
}

.hamburger-menu {
  padding: 0.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.hamburger-menu i {
  color: var(--button-color);
}

.nav-menu {
  display: none;
  gap: 1rem;
}

.nav-menu a {
  font-size: 1rem;
  color: var(--text-color-1);
  transition: all 0.2s;
}

.nav-menu a:hover {
  color: var(--text-color-1-hover);
}

.form-search {
  display: none;
}

.form-search {
  justify-content: end;
  gap: 0.5rem;
  width: 100%;
}

.form-control {
  width: 100%;
  padding: 0.8rem 1rem;
  font-size: 0.9rem;
  border-radius: 0.2rem;
  border: 1px solid var(--form-control-border);
