  color: var(--text-color-2);
  background-color: var(--button-color);
  border: none;
  outline: none;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: all 0.2s;
}

.button-search:hover {
  background-color: var(--button-color-hover);
}

.dropdown-menu {
  position: absolute;
  padding: 2rem 1rem;
  top: 1000px;
  left: 1rem;
  right: 1rem;
  background-color: var(--button-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 0.5rem;
