,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;IAClB,WAAW;IACX,aAAa;EACf;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,qBAAqB;EACvB;AACF;;AAEA;EACE;IACE,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,cAAc;EAChB;;EAEA;IACE,aAAa;IACb,cAAc;IACd,qBAAqB;IACrB,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,eAAe;IACf,aAAa;IACb,cAAc;EAChB;;EAEA;IACE,aAAa;IACb,cAAc;IACd,UAAU;EACZ;;EAEA;IACE,aAAa;IACb,YAAY;IACZ,cAAc;IACd,qBAAqB;IACrB,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,SAAS;IACT,WAAW;EACb;;EAEA;IACE,aAAa;IACb,wBAAwB;IACxB,UAAU;EACZ;;EAEA;IACE,UAAU;IACV,iBAAiB;IACjB,aAAa;EACf;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,cAAc;IACd,kBAAkB;IAClB,aAAa;IACb,aAAa;EACf;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,qBAAqB;EACvB;AACF","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');\r\n\r\n* {\r\n  padding: 0;\r\n  margin: 0;\r\n  box-sizing: border-box;\r\n  font-family: 'Poppins', sans-serif;\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n:root {\r\n  --primary-color: #fff;\r\n  --dropdown-hover: #f5ebe0;\r\n  --text-color-1: #20201d;\r\n  --text-color-2: #fff;\r\n  --text-color-1-hover: #b7965c;\r\n  --border-input: #20201d;\r\n  --border-input-focus: #b7965c;\r\n  --button-color: #b7965c;\r\n  --button-color-hover: #af7d27;\r\n  --form-control-border: #cccc;\r\n  --scrollbar-color: #424168;\r\n  --panel-color: #eaeaea;\r\n}\r\n\r\nbody {\r\n  background-color: var(--primary-color);\r\n}\r\n\r\n/* ============= TOATS ============= */\r\n\r\n.toast-success {\r\n  background-color: #5a58a4 !important;\r\n}\r\n\r\n.toast-warning {\r\n  background-color: #f0cd81 !important;\r\n}\r\n\r\n.toast-error {\r\n  background-color: #ff537e !important;\r\n}\r\n\r\n/* ============= BUTTON =========== */\r\n.button {\r\n  padding: 0.8rem 1rem;\r\n  font-size: 0.9rem;\r\n  color: var(--text-color-2);\r\n  background-color: var(--button-color);\r\n  border: none;\r\n  outline: none;\r\n  border-radius: 0.2rem;\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n}\r\n\r\n.button:hover {\r\n  background-color: var(--button-color-hover);\r\n}\r\n\r\n.button-icon {\r\n  background-color: var(--button-color);\r\n  border-radius: 0.2rem;\r\n  border: none;\r\n  outline: none;\r\n  padding: 0.6rem 0.7rem;\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n}\r\n\r\n.button-icon:hover {\r\n  background-color: var(--button-color-hover);\r\n}\r\n\r\n.button-icon i {\r\n  font-size: 1.5rem;\r\n  color: var(--text-color-2);\r\n}\r\n\r\n.button-close {\r\n  position: absolute;\r\n  top: 0;\r\n  right: 0;\r\n  border-radius: 0 0.5rem 0 0.5rem;\r\n  padding: 0.6rem 0.85rem;\r\n}\r\n\r\n/* ============= SEARCH =========== */\r\n\r\n.search-title {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.search-title h2.title {\r\n  font-size: 2rem;\r\n  color: var(--button-color);\r\n  margin-bottom: 0;\r\n}\r\n\r\n.search-title .search-button-back {\r\n  background: transparent;\r\n  border: none;\r\n  outline: none;\r\n  padding: 0.5rem 0.6rem;\r\n  cursor: pointer;\r\n}\r\n\r\n.search-title .search-button-back i {\r\n  font-size: 1.8rem;\r\n  color: var(--button-color);\r\n}\r\n\r\n/* =========== LOADER ===========  */\r\n\r\n.loader-box {\r\n  position: relative;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.loader {\r\n  position: absolute;\r\n  left: 135px;\r\n  display: none;\r\n  align-items: center;\r\n  transition: opacity 0.75s, visibility 0.75s;\r\n}\r\n\r\n.loader-hidden {\r\n  opacity: 0;\r\n  visibility: hidden;\r\n}\r\n\r\n.loader::after {\r\n  content: '';\r\n  width: 16px;\r\n  height: 16px;\r\n  border: 6px solid #dddddd;\r\n  border-top-color: var(--scrollbar-color);\r\n  border-radius: 50%;\r\n  animation: loading 0.75s ease infinite;\r\n}\r\n\r\n@keyframes loading {\r\n  from {\r\n    transform: rotate(0turn);\r\n  }\r\n  to {\r\n    transform: rotate(1turn);\r\n  }\r\n}\r\n\r\n/* ============= NAVBAR =========== */\r\nheader {\r\n  position: sticky;\r\n  top: 0;\r\n  z-index: 10;\r\n  padding: 1rem 1rem;\r\n  background-color: var(--primary-color);\r\n}\r\n\r\nnav {\r\n  width: 100%;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.logo,\r\n.nav-menu a,\r\n.dropdown-menu a {\r\n  text-decoration: none;\r\n}\r\n\r\n.nav-menu a,\r\n.dropdown-menu a {\r\n  text-decoration: none;\r\n  padding: 0.7rem 0;\r\n}\r\n\r\n.logo {\r\n  width: 8rem;\r\n}\r\n\r\n.hamburger-menu {\r\n  padding: 0.3rem;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  border: none;\r\n  outline: none;\r\n  background-color: transparent;\r\n  cursor: pointer;\r\n}\r\n\r\n.hamburger-menu i {\r\n  color: var(--button-color);\r\n}\r\n\r\n.nav-menu {\r\n  display: none;\r\n  gap: 1rem;\r\n}\r\n\r\n.nav-menu a {\r\n  font-size: 1rem;\r\n  color: var(--text-color-1);\r\n  transition: all 0.2s;\r\n}\r\n\r\n.nav-menu a:hover {\r\n  color: var(--text-color-1-hover);\r\n}\r\n\r\n.form-search {\r\n  display: none;\r\n}\r\n\r\n.form-search {\r\n  justify-content: end;\r\n  gap: 0.5rem;\r\n  width: 100%;\r\n}\r\n\r\n.form-control {\r\n  width: 100%;\r\n  padding: 0.8rem 1rem;\r\n  font-size: 0.9rem;\r\n  border-radius: 0.2rem;\r\n  border: 1px solid var(--form-control-border);\r\n  outline: none;\r\n}\r\n\r\n.form-control:focus {\r\n  border-color: var(--border-input-focus);\r\n  box-shadow: 0 0 5px rgba(183, 150, 92, 0.5);\r\n}\r\n\r\n.button-search {\r\n  padding: 0.8rem 1rem;\r\n  font-size: 0.9rem;\r\n  color: var(--text-color-2);\r\n  background-color: var(--button-color);\r\n  border: none;\r\n  outline: none;\r\n  border-radius: 0.2rem;\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n}\r\n\r\n.button-search:hover {\r\n  background-color: var(--button-color-hover);\r\n}\r\n\r\n.dropdown-menu {\r\n  position: absolute;\r\n  padding: 2rem 1rem;\r\n  top: 1000px;\r\n  left: 1rem;\r\n  right: 1rem;\r\n  background-color: var(--button-color);\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 1rem;\r\n  border-radius: 0.5rem;\r\n  opacity: 0;\r\n  max-height: 0;\r\n  overflow: hidden;\r\n  transition: opacity 0.3s ease, max-height 0.3s ease;\r\n}\r\n\r\n.dropdown-menu.active {\r\n  top: 95px;\r\n  opacity: 1;\r\n  max-height: 500px;\r\n}\r\n\r\n.dropdown-menu a {\r\n  color: var(--text-color-2);\r\n  transition: all 0.2s;\r\n}\r\n\r\n.dropdown-menu a:hover {\r\n  color: var(--dropdown-hover);\r\n}\r\n\r\n.form-dropdown {\r\n  display: flex;\r\n}\r\n\r\n.button-dropdown {\r\n  background-color: var(--text-color-2);\r\n  color: var(--button-color);\r\n}\r\n\r\n.button-dropdown:hover {\r\n  background-color: var(--dropdown-hover);\r\n}\r\n\r\n/* ============= MAIN ============= */\r\n\r\n.main {\r\n  display: flex;\r\n  flex-direction: column;\r\n  min-height: calc(100vh - 84px);\r\n}\r\n\r\nmain {\r\n  flex: 1;\r\n}\r\n\r\n/* ============= JUMBOTRON ============= */\r\n\r\n.jumbotron {\r\n  width: 100%;\r\n  height: 80vh;\r\n  background-size: cover;\r\n  background-position: center;\r\n}\r\n\r\n.jumbotron-overlay {\r\n  padding: 1rem 1rem;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  flex-direction: column;\r\n}\r\n\r\n.jumbotron-title {\r\n  color: var(--text-color-2);\r\n  text-align: center;\r\n  font-size: 3.5rem;\r\n}\r\n\r\n.jumbotron-subtitle {\r\n  color: var(--text-color-2);\r\n  text-align: center;\r\n  font-size: 1rem;\r\n}\r\n\r\n.restaurant-list {\r\n  padding: 3rem 1rem;\r\n  width: 100%;\r\n}\r\n\r\n.restaurant-list h2 {\r\n  text-align: center;\r\n  color: var(--button-color);\r\n  margin-bottom: 2rem;\r\n  font-size: 2rem;\r\n}\r\n\r\n.cards-box {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 1rem;\r\n}\r\n\r\n.cards-box .error {\r\n  font-size: 1.2rem;\r\n  color: var(--text-color-1);\r\n  font-style: italic;\r\n  font-weight: 300;\r\n}\r\n\r\na.card {\r\n  height: fit-content;\r\n  position: relative;\r\n  width: 100%;\r\n  padding: 1rem;\r\n  border-radius: 0.5rem;\r\n  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\r\n  text-decoration: none;\r\n  color: var(--text-color-1);\r\n}\r\n\r\n.card img {\r\n  width: 100%;\r\n  height: 12rem;\r\n  object-fit: cover;\r\n  border-radius: 0.2rem;\r\n}\r\n\r\n.city {\r\n  position: absolute;\r\n  top: 1rem;\r\n  left: 1rem;\r\n  border-radius: 0.2rem 0 0.2rem 0;\r\n  background-color: var(--button-color);\r\n  padding: 5px 10px 5px;\r\n  color: white;\r\n  font-weight: 500;\r\n}\r\n\r\n.card-rating {\r\n  display: flex;\r\n  justify-content: start;\r\n  align-items: center;\r\n  gap: 0.3rem;\r\n}\r\n\r\n.card-rating i {\r\n  color: var(--button-color);\r\n  font-size: 1.2rem;\r\n}\r\n\r\n.card-rating a {\r\n  text-decoration: none;\r\n  font-size: 1rem;\r\n  color: var(--text-color-1);\r\n}\r\n\r\n.card-title {\r\n  color: var(--text-color-1);\r\n  font-size: 1.5rem;\r\n}\r\n\r\n.card-desc {\r\n  color: var(--text-color-1);\r\n  font-size: 1rem;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 5;\r\n  -webkit-box-orient: vertical;\r\n  overflow: hidden;\r\n}\r\n\r\n.card-desc.active {\r\n  display: unset;\r\n  -webkit-line-clamp: unset;\r\n  -webkit-box-orient: unset;\r\n  overflow: unset;\r\n}\r\n\r\n.button-read-more {\r\n  margin-top: 1rem;\r\n}\r\n\r\n/* ============= FOOTER ============= */\r\n\r\nfooter {\r\n  padding: 0 1rem;\r\n}\r\n\r\n.footer-box {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1rem;\r\n  justify-content: space-between;\r\n}\r\n\r\n.footer-card {\r\n  width: 100%;\r\n}\r\n\r\n.footer-card img {\r\n  width: 8rem;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.footer-card p {\r\n  line-height: 1.5rem;\r\n  font-size: 1rem;\r\n  color: var(--text-color-1);\r\n}\r\n\r\n.footer-desc {\r\n  width: 20rem;\r\n}\r\n\r\n.footer-nav-menu {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.footer-nav-menu h4 {\r\n  font-size: 1.4rem;\r\n  color: var(--button-color);\r\n}\r\n\r\n.footer-nav-menu a {\r\n  padding: 0.7rem 0.7rem 0.7rem 0;\r\n  font-size: 1rem;\r\n  font-weight: 400;\r\n  color: var(--text-color-1);\r\n  text-decoration: none;\r\n  transition: all 0.2s;\r\n}\r\n\r\n.footer-nav-menu a:hover {\r\n  color: var(--button-color-hover);\r\n}\r\n\r\n.footer-nav-menu p {\r\n  padding: 0.7rem 0.7rem 0.7rem 0;\r\n  font-size: 1rem;\r\n  font-weight: 400;\r\n}\r\n\r\n.copyright {\r\n  font-size: 1rem;\r\n  font-weight: 400;\r\n  padding: 1rem;\r\n  text-align: center;\r\n}\r\n\r\n.skip-content {\r\n  position: absolute;\r\n  top: -40px;\r\n  left: 0;\r\n  background-color: var(--button-color);\r\n  border: none;\r\n  border-radius: 0 0 0.3rem 0;\r\n  outline: none;\r\n  text-decoration: none;\r\n  color: white;\r\n  padding: 8px;\r\n  z-index: 20;\r\n}\r\n\r\n.skip-content:focus {\r\n  top: 0;\r\n}\r\n\r\n/* ============= DETAIL =========== */\r\n\r\n.detail-box {\r\n  width: 100%;\r\n  background-size: cover;\r\n  background-position: center;\r\n}\r\n\r\n.detail-overlay {\r\n  position: relative;\r\n  padding: 3rem 1rem;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 2rem;\r\n  align-content: center;\r\n}\r\n\r\n.detail-button-favorit {\r\n  position: absolute;\r\n  top: 3.5rem;\r\n  right: 1.5rem;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n  z-index: 2;\r\n}\r\n\r\n.detail-title {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.detail-title .detail-button-back {\r\n  padding: 0.5rem;\r\n  background: transparent;\r\n  border: none;\r\n  outline: none;\r\n  cursor: pointer;\r\n}\r\n\r\n.detail-button-back i {\r\n  color: var(--text-color-2);\r\n  font-size: 2.2rem;\r\n}\r\n\r\n.detail-title h2 {\r\n  font-size: 2.2rem;\r\n  color: var(--text-color-2);\r\n}\r\n\r\n.detail-content h6 {\r\n  padding-left: 54.8px;\r\n  font-size: 0.9rem;\r\n  font-weight: 500;\r\n  color: var(--text-color-2);\r\n}\r\n\r\n.detail-address-box {\r\n  background-color: var(--primary-color);\r\n  padding: 1rem;\r\n  margin-top: 1rem;\r\n  border-radius: 0.5rem;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  gap: 1rem;\r\n}\r\n\r\n.detail-address {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.detail-address p {\r\n  font-weight: 400;\r\n}\r\n\r\n.detail-address i {\r\n  font-size: 1.2rem;\r\n  color: var(--button-color);\r\n}\r\n\r\nhr {\r\n  width: 2px;\r\n  background-color: var(--button-color);\r\n  border: none;\r\n}\r\n\r\n.detail-description {\r\n  margin-top: 1rem;\r\n  font-weight: 300;\r\n  color: var(--text-color-2);\r\n}\r\n\r\n.detail-content-image {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.detail-content-image img {\r\n  width: 100%;\r\n  border-radius: 0.5rem;\r\n}\r\n\r\n/* ============= MENU ============= */\r\n\r\n.menu-box {\r\n  margin-top: 3rem;\r\n  padding: 0 1rem;\r\n}\r\n\r\n.menu-box h2 {\r\n  text-align: center;\r\n  color: var(--button-color);\r\n  margin-bottom: 2rem;\r\n  font-size: 2.2rem;\r\n}\r\n\r\n.menu-content {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 1rem;\r\n}\r\n\r\n.menu-title {\r\n  margin-bottom: 0.5rem;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.menu-title h3 {\r\n  font-size: 1.6rem;\r\n  color: var(--scrollbar-color);\r\n}\r\n\r\n.menu-title i {\r\n  font-size: 1.5rem;\r\n  color: var(--scrollbar-color);\r\n}\r\n\r\n.menus-content p {\r\n  font-size: 1rem;\r\n  color: var(--text-color);\r\n  line-height: 1.5rem;\r\n}\r\n\r\n.food {\r\n  padding: 1rem;\r\n  background-color: var(--primary-color);\r\n  border-radius: 0.5rem;\r\n  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;