___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --dark: #e5e5e5;
  --light: #ffffff;
  /* --success: #0abf30; */
  --success: #5c5b8c;
  --error: #e24d4c;
  --warning: #e9bd0c;
  --info: #3498db;
}

.notifications {
  position: fixed;
  z-index: 9999;
  top: 30px;
  right: 20px;
}

.notifications :where(.toast, .column) {
  display: flex;
  align-items: center;
}

.notifications .toast {
  width: 400px;
  position: relative;
  overflow: hidden;
  list-style: none;
  /* border: 1px solid var(--dark); */
  border-radius: 4px;
  padding: 16px 17px;
  margin-bottom: 10px;
  background: var(--light);
  justify-content: space-between;
  animation: show_toast 0.3s ease forwards;
}

.notifications .toast.success {
  border: 1px solid var(--success);
  background-color: var(--success);
}

.notifications .toast.info {
  border: 1px solid var(--info);
  background-color: var(--info);
}

.notifications .toast.warning {
  border: 1px solid var(--warning);
  background-color: var(--warning);
}

.notifications .toast.error {
  border: 1px solid var(--error);
  background-color: var(--error);
}

@keyframes show_toast {
  0% {
    transform: translateX(100%);
  }
  40% {
    transform: translateX(-5%);
  }
  80% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-10px);
  }
}

.notifications .toast.hide {
  animation: hide_toast 0.3s ease forwards;
}

@keyframes hide_toast {
  0% {
    transform: translateX(-10px);
  }
  40% {
    transform: translateX(0%);
  }
  80% {
    transform: translateX(-5%);
  }
  100% {
    transform: translateX(calc(100% + 20px));
  }
}

.toast::before {
  position: absolute;
  background: var(--light);
  content: '';
  height: 3px;
  width: 100%;
  bottom: 0px;
  left: 0px;
  animation: progress 5s linear forwards;
}

@keyframes progress {
  100% {
    width: 0%;
  }
}

/* .toast.success::before,
.btn#success {
  background: var(--light);
}

.toast.error::before,
.btn#error {
  background: var(--light);
}

.toast.warning::before,
.btn#warning {
  background: var(--light);
}

.toast.info::before,
.btn#info {
  background: var(--light);
} */

.toast .column i {
  font-size: 1.75rem;
  color: var(--light);
}

/* .toast.success .column i {
  color: var(--light);
}

.toast.error .column i {
  color: var(--light);
}

.toast.warning .column i {
  color: var(--light);
}

.toast.info .column i {
  color: var(--light);
} */

.toast .column span {
  color: var(--light);
  font-size: 1rem;
  margin-left: 12px;
}

.toast i:last-child {
  padding-left: 0.5rem;
  color: var(--light);
  cursor: pointer;
  transition: all 0.2s;
}

.toast i:last-child:hover {
  color: var(--dark);
}

.buttons .btn {
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 5px;
  color: var(--light);
  font-size: 1.2rem;
  padding: 10px 20px;
  border-radius: 4px;
}

@media screen and (max-width: 530px) {
  .notifications {
    width: 95%;
  }

  .notifications .toast {
    width: 100%;
    font-size: 1rem;
    margin-left: 20px;
  }

  .buttons .btn {
    margin: 0 1px;
    font-size: 1.1rem;
    padding: 8px 15px;
  }
}
